import React, {ChangeEvent, useEffect, useState} from "react";
import s from "../stylesForClock/timer.module.css";
import {Select} from "@mui/material";
import {SelectChangeEvent} from "@mui/material/Select";
import Button from "@mui/material/Button";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";

const Timer = () => {
    const [days, setDays] = useState(0);
    const [hours, setHours] = useState(0);
    const [minutes, setMinutes] = useState(0);
    const [seconds, setSeconds] = useState(0);
    const [inputDays, setInputDays] = useState(0);
    const [inputHours, setInputHours] = useState(0);
    const [inputMinutes, setInputMinutes] = useState(0);
    const [inputSeconds, setInputSeconds] = useState(0);
    const [isOn, setIsOn] = useState(false);
    const [open, setOpen] = useState(false);

    const onReset = () => {
        setIsOn(false);
        setInputDays(0);
        setInputHours(0);
        setInputMinutes(0);
        setInputSeconds(0);
        setDays(0);
        setHours(0);
        setMinutes(0);
        setSeconds(0);
    };

    useEffect(() => {
        let interval: NodeJS.Timer;
        if (isOn) {
            interval = setInterval(() => {
                setSeconds((prevSeconds) => {
                    if (prevSeconds > 0) {
                        return prevSeconds - 1;
                    } else if (minutes > 0) {
                        setMinutes((prevMinutes) => prevMinutes - 1);
                        return 59;
                    } else if (hours > 0) {
                        setHours((prevHours) => prevHours - 1);
                        setMinutes(59);
                        return 59;
                    } else if (days > 0) {
                        setDays((prevDays) => prevDays - 1);
                        setHours(23);
                        setMinutes(59);
                        return 59;
                    } else {
                        clearInterval(interval);
                        setIsOn(false);
                        return 0;
                    }
                });
            }, 1000);
        }


        return () => clearInterval(interval);
    }, [isOn, minutes, hours, days]);

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const daysArray = Array.from({length: 32}, (_, i) => i);
    const hoursArray = Array.from({length: 24}, (_, i) => i);
    const minutesArray = Array.from({length: 60}, (_, i) => i);
    const secondsArray = Array.from({length: 60}, (_, i) => i);

    const selectDays = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputDays(value);
    };
    const selectHours = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputHours(value);
    };
    const selectMinutes = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputMinutes(value);
    };
    const selectSeconds = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputSeconds(value);
    };

    const startTimer = () => {
        setDays(inputDays);
        setHours(inputHours);
        setMinutes(inputMinutes);
        setSeconds(inputSeconds);
        setIsOn(true);
    };

    return (
        <div className={s.timerBody}>

            <div className={s.timerWrapper}>
                <span className={s.timerTitle}>
                    Timer
                </span>
                <div className={s.mainTimerCircle}>
                    <div className={s.secondTimerCircle}>
                        <div className={s.timerInner}>
                            <div className={s.timerSegment}>
                                <span className={s.time}>{days}</span>
                                <span className={s.label}>Days</span>
                            </div>

                            <div className={s.timerSegment}>
                                <span className={s.time}>{hours}</span>
                                <span className={s.label}>Hours</span>
                            </div>

                            <div className={s.timerSegment}>
                                <span className={s.time}>{minutes}</span>
                                <span className={s.label}>Minutes</span>
                            </div>

                            <div className={s.timerSegment}>
                                <span className={s.time}>{seconds}</span>
                                <span className={s.label}>Seconds</span>
                            </div>

                        </div>
                    </div>
                </div>

                <div className={s.inputs}>

                    <FormControl sx={{
                        m: 1, minWidth: 120,
                        '& .MuiSelect-select': {
                            color: '#fff', //  
                        },
                        '& .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#fff', //  
                        },
                        '&:hover .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '& .MuiInputLabel-root': {
                            color: '#fff', //  
                        },
                        '&.Mui-focused .MuiInputLabel-root': {
                            color: '#9b9b9b', //    
                        },
                        '& .MuiSvgIcon-root': {
                            color: '#9b9b9b', //   (,  )
                        },
                        '& .MuiMenuItem-root': {
                            color: '#fff', //    
                        },
                        '& .MuiMenuItem-root:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.1)', //    
                        },
                        '& .MuiMenuItem-root.Mui-selected': {
                            backgroundColor: 'rgba(255, 255, 255, 0.2)', //    
                        },


                    }}>
                        <InputLabel id="demo-controlled-open-select-label">Days</InputLabel>
                        <Select
                            labelId="demo-controlled-open-select-label"
                            className={s.select}
                            id="Days-controlled-open-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputDays}
                            label="Days"
                            onChange={selectDays}
                        >
                            {daysArray.map((day) => (
                                <MenuItem key={day} value={day}>
                                    {day}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>


                    <FormControl sx={{
                        m: 1, minWidth: 120,
                        '& .MuiSelect-select': {
                            color: '#fff', //  
                        },
                        '& .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#fff', //  
                        },
                        '&:hover .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '& .MuiInputLabel-root': {
                            color: '#fff', //  
                        },
                        '&.Mui-focused .MuiInputLabel-root': {
                            color: '#9b9b9b', //    
                        },
                        '& .MuiSvgIcon-root': {
                            color: '#9b9b9b', //   (,  )
                        },
                        '& .MuiMenuItem-root': {
                            color: '#fff', //    
                        },
                        '& .MuiMenuItem-root:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.1)', //    
                        },
                        '& .MuiMenuItem-root.Mui-selected': {
                            backgroundColor: 'rgba(255, 255, 255, 0.2)', //    
                        },


                    }}>
                        <InputLabel id="demo-controlled-open-select-label">Hours</InputLabel>
                        <Select
                            labelId="demo-controlled-open-select-label"
                            id="demo-controlled-open-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputHours}
                            label="Hours"
                            onChange={selectHours}
                        >
                            {hoursArray.map((hour) => (
                                <MenuItem key={hour} value={hour}>
                                    {hour}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>


                    <FormControl sx={{
                        m: 1, minWidth: 120,
                        '& .MuiSelect-select': {
                            color: '#fff', //  
                        },
                        '& .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#fff', //  
                        },
                        '&:hover .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '& .MuiInputLabel-root': {
                            color: '#fff', //  
                        },
                        '&.Mui-focused .MuiInputLabel-root': {
                            color: '#9b9b9b', //    
                        },
                        '& .MuiSvgIcon-root': {
                            color: '#9b9b9b', //   (,  )
                        },
                        '& .MuiMenuItem-root': {
                            color: '#fff', //    
                        },
                        '& .MuiMenuItem-root:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.1)', //    
                        },
                        '& .MuiMenuItem-root.Mui-selected': {
                            backgroundColor: 'rgba(255, 255, 255, 0.2)', //    
                        },


                    }}>
                        <InputLabel id="demo-controlled-open-select-label">Minutes</InputLabel>
                        <Select
                            labelId="demo-controlled-open-select-label"
                            id="demo-controlled-open-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputMinutes}
                            label="Minutes"
                            onChange={selectMinutes}
                        >
                            {minutesArray.map((min) => (
                                <MenuItem key={min} value={min}>
                                    {min}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>


                    <FormControl sx={{
                        m: 1, minWidth: 120,
                        '& .MuiSelect-select': {
                            color: '#fff', //  
                        },
                        '& .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#fff', //  
                        },
                        '&:hover .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                            borderColor: '#9b9b9b', //    
                        },
                        '& .MuiInputLabel-root': {
                            color: '#fff', //  
                        },
                        '&.Mui-focused .MuiInputLabel-root': {
                            color: '#9b9b9b', //    
                        },
                        '& .MuiSvgIcon-root': {
                            color: '#9b9b9b', //   (,  )
                        },
                        '& .MuiMenuItem-root': {
                            color: '#fff', //    
                        },
                        '& .MuiMenuItem-root:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.1)', //    
                        },
                        '& .MuiMenuItem-root.Mui-selected': {
                            backgroundColor: 'rgba(255, 255, 255, 0.2)', //    
                        },


                    }}>
                        <InputLabel id="demo-controlled-open-select-label">Seconds</InputLabel>
                        <Select
                            labelId="demo-controlled-open-select-label"
                            id="demo-controlled-open-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputSeconds}
                            label="Seconds"
                            onChange={selectSeconds}
                        >
                            {secondsArray.map((sec) => (
                                <MenuItem key={sec} value={sec}>
                                    {sec}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <div className={s.buttons}>
                        <button
                            className={s.resetButton}
                            onClick={onReset}
                            disabled={!isOn && seconds === 0}
                        >
                            Reset
                        </button>
                        {!isOn && (
                            <button className={s.startButton} onClick={startTimer}>
                                Start
                            </button>
                        )}
                        {isOn && (
                            <button className={s.stopButton} onClick={() => setIsOn(false)}>
                                Stop
                            </button>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Timer;
