import React from 'react';
import './App.css';
import Clock from "./components/clock";
import Stopwatch from "./components/stopwatch";
import { NavLink, Route, Routes} from "react-router-dom";
import Timer from "./components/timer";

const PATH = {
    clock: '/clock',
    stopwatch: '/stopwatch',
    timer:'/timer',
};

function App() {
    return (
        <>
        <div className="App">
            <Routes>
                <Route path={PATH.clock} element={<Clock/>} />
                <Route path={PATH.stopwatch} element={<Stopwatch/>} />
                <Route path={PATH.timer} element={<Timer/>} />
                <Route index path={'*'} element={<Main/>} />
            </Routes>
        </div>
        </>
    );
}

function Main() {
    return (
        <div className="App-body">
            <span className="App-title"></span>
            <div className="App-links">
                <NavLink to={PATH.clock} className="App-link">
                    <h2>Clock</h2>
                </NavLink>
                <NavLink to={PATH.stopwatch} className="App-link">
                    <h2>Stopwatch</h2>
                </NavLink>
                <NavLink to={PATH.timer} className="App-link">
                    <h2>Timer</h2>
                </NavLink>
            </div>
            <span className="App-span">jsxquatro</span>
        </div>
    );
}

export default App;




